@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300;400&display=swap');


.bottomright {
    position: fixed;
    right: 0px; bottom: 0px;
    background-color:lightblue;
    padding: 5px;
    z-index: 999;
  }

 
  
  section.chat_section{
      width: 600px;
      max-width: 100%;
      background: #fff;
      box-shadow: 0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0, 0, 0,0.05)
  }
  
  .brand{
      padding: 20px;
      background: #f1f1f1;
      display: flex;
      align-items: center;
  }
  
  .brand h1{
      text-transform: uppercase;
      font-size: 20px;
      color:#444;
      margin-left: 10px;
  }
  
  .message_area{
      height: 500px;
      padding: 16px;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      padding-top: 40px;
  }
  
  textarea{
      width:100%;
      border:none;
      padding:20px;
      font-size: 16px;
      outline: none;
      background: #FBFBFB;
      resize: none;
  }
  
  .message{
      padding: 20px;
      border-radius: 4px;
      margin-bottom:40px;
      max-width: 300px;
      position: relative;
  }
  
  .incoming{
      background: #8F8BEB;
      color:#fff;
  }
  
  .outgoing{
      background: #e9eafd;
      color:#787986;
      margin-left: auto;
  
  }
  
  .message h4{
      position: absolute;
      top:-20px;
      left:0;
      color:#333;
      font-size: 14px;
  }
  
  

